import { ReactNode } from 'react';
import LinkTo from './LinkTo/LinkTo';

interface Props {
  children?: ReactNode;
  className?: string;
  display: 'block' | 'inline-block' | 'flex' | 'inline-flex' | 'inline';
  href: string;
  color?: 'primary' | 'warning' | string;
  isNativeAnchor?: boolean;
  target?: '_blank' | '_self' | '_parent' | '_top';
}

export default function ButtonLink(props: Props) {
  const { href, display, children, className, color, isNativeAnchor, target } =
    props;
  if (isNativeAnchor) {
    return (
      <a
        target={!target ? '_blank' : target}
        rel="noopener noreferrer"
        href={href}
        className={`${className || ''} ${display || 'block'}
          rounded-lg bg-${
            color || 'primary'
          } p-3 px-4 duration-150 hover:-translate-y-px hover:-translate-x-px
        hover:shadow-2xl 
          `}
      >
        {children}
      </a>
    );
  }
  return (
    <LinkTo
      href={href}
      className={`${className || ''} ${display || 'block'}
     rounded-lg bg-${
       color || 'primary'
     } p-3 px-4 duration-150 hover:-translate-y-px hover:-translate-x-px
   hover:shadow-2xl 
     `}
    >
      {children}
    </LinkTo>
  );
}
