import { lazy, Suspense } from 'react';
import { ToastContainer } from 'react-toastify';
import ReactGA from 'react-ga';
import { Routes, Route, useLocation, useNavigate } from 'react-router-dom';
import TransitionLayer from './atoms/TransitionLayer/TransitionLayer';
import Booth from './pages/Booth/Booth';

const EmployerPage = lazy(() => import('./pages/EmployerPage/EmployerPage'));
const Lobby = lazy(() => import('./pages/Lobby/Lobby'));
const IVFAcademyPage = lazy(
  () => import('./pages/IVFAcademyPage/IVFAcademyPage')
);
const LandingPage = lazy(() => import('./pages/LandingPage/LandingPage'));
const NotFound = lazy(() => import('./pages/NotFound/NotFound'));
const VacancyPage = lazy(() => import('./pages/VacancyPage/VacancyPage'));
const ExpoHall = lazy(() => import('./pages/ExpoHall/ExpoHall'));
const WebinarSeriesPage = lazy(
  () => import('./pages/WebinarSeriesPage/WebinarSeriesPage')
);
const EntrepreneurshipExpo = lazy(
  () => import('./pages/EntrepreneurshipExpo/EntrepreneurshipExpo')
);

ReactGA.initialize('UA-49614634-4');

export default function App() {
  const location = useLocation();
  const navigate = useNavigate();

  // if (isMobile) {
  //   return <OpenInComputer />;
  // }

  return (
    <>
      <ToastContainer />
      <Suspense fallback={<TransitionLayer showTransition={false} />}>
        <Routes key={location.pathname} location={location}>
          <Route path="/" element={<LandingPage />} />
          <Route path="/employer" element={<EmployerPage />} />
          <Route path="/ivf-academy" element={<IVFAcademyPage />} />
          <Route path="/webinar-series" element={<WebinarSeriesPage />} />
          <Route path="/vacancy" element={<VacancyPage />} />
          <Route path="/expo-hall" element={<Lobby />} />
          <Route path="/expo-area" element={<ExpoHall />} />
          <Route
            path="/entrepreneurship-expo"
            element={<EntrepreneurshipExpo />}
          />
          <Route path="/booth/:companyId" element={<Booth />} />
          <Route path="*" element={<NotFound />} />
        </Routes>
      </Suspense>
    </>
  );
}
