import { useContext } from 'react';
import { AppContext } from '../../../context/AppContext';
import BackButton3D from '../../molecules/BackButton3D/BackButton3D';
import Menu3D from '../Menu3D/Menu3D';

export default function Navigation3D({ href }: { href: string }) {
  const { toggleMenu } = useContext(AppContext);
  const ctx = useContext(AppContext);

  return (
    <>
      {ctx.showMenu3D && <Menu3D />}
      <BackButton3D href={href} />
      <div className="absolute top-5 right-5">
        <button
          type="button"
          className="h-10 w-10 rounded-lg bg-white text-primary shadow ring-white ring-offset-2 ring-offset-gray-800 hover:ring-2"
          title="Close"
          onClick={() => toggleMenu(true)}
        >
          <i className="bi bi-list" />
        </button>
      </div>
    </>
  );
}
