import { ICompanyDetail } from '../../../../interfaces';

const aiesec: ICompanyDetail = {
  id: 'aiesec',
  name: 'AIESEC',
  address:
    '',
  boothType: '1x1_1',
  boothBackground: '/img/booth/aiesec.png',
  contacts: [
    {
      icon: 'instagram',
      title: '@aiesecipb',
    },
    {
      icon: 'globe',
      title: 'https://aiesec.or.id/',
    },
  ],
  image: '/img/logos/aiesec_free.png',
  profile:
    `<p style="text-align:center;font-weight:bold;">Who Are We ?</p>
    AIESEC is the largest youth-led organization in the world, facilitating professional and voluntary leadership opportunities for over 73 years. We develop youth in a global learning environment that consists of over 120 countries and territories.<br>
    <p style="text-align:center;font-weight:bold;">What is AIESEC in IPB</p>
    As an expansion from AIESEC in Universitas Indonesia (UI), AIESEC in Institut Pertanian Bogor (IPB) facilitates leadership opportunities to students in Bogor, especially in IPB.<br><br>
    
    AIESEC is a Global, Independent, Non-Political, Not-For-Profit Youth-Run Organization. AIESEC does not discriminate on the base of Gender, Sexual Orientation, Disabilities Creed, or Religion, Nor on the basis of National, Ethnic, or Social Origini. <br><br>

    AIESEC believes in achieving our vision, peace, and fulfillment of humankind's potential by activating leadership qualities in youth through learning from practical experiences in challenging environments.
    
    <p style="text-align:center;font-weight:bold;font-size:40px;">Internship</p><br>

    Global Talent merupakan program magang ke luar negeri yang ditawarkan oleh AIESEC in IPB, terbuka untuk para mahasiswa maupun fresh graduates yang berumur 18-30 tahun.<br><br>

    AIESEC bekerja sama dengan berbagai multinational company (MNC), Start-up, maupun small medium-sized enterprise (SME) dari berbagai negara seperti Hong Kong, Jerman dan Swedia. Tentunya, program ini menyediakan gaji atau akomodasi tempat tinggal selama disana.<br><br>

    Selama proses pendaftaran, akan disediakan EP manager yang membantu kalian dalam proses application, seperti CV & Interview Training, Preparation Support (Visa, Documents, etc.), dan Country and Company information. <br><br>

    Field yang saat ini tersedia :<br>
    - Business administration<br>
    - Business development<br>
    - Marketing<br>
    - Engineering<br>
    - Finance<br>
    - IT<br><br>

    Benefit yang akan didapat<br>
    - Kesempatan untuk meningkatkan self-value dan nilai karir secara drastis<br>
    - Eksposur ke lingkungan kerja Internasional<br>
    - Pengembangan skill kepemimpinan <br>
    - Kesempatan magang yang berbayar
    <br><br>
    Daftarkan diri kalian di link berikut:<br>
    https://aiesec.org/
    <br><br>
    Untuk informasi lebih lanjut klik link booklet!<br>
    https://ipb.link/globaltalent-handbook
   
    `,
};

