import Button from '../../atoms/Button';

export default function BackButton3D({ href }: { href: string }) {
  return (
    <div className="absolute top-5 left-5">
      <Button
        onClick={() => window.history.back()}
        display="inline-block"
        className="bg-opacity-50 text-white shadow"
      >
        Kembali
      </Button>
    </div>
  );
}
