export default function LinkTransformer({ link }: { link: string }) {
  if (link.includes('http')) {
    return (
      <span>
        <a
          href={link}
          target="_blank"
          className="underline"
          rel="noopener noreferrer"
        >
          {link.length < 30 ? link : `${link.slice(0, 30)}...`}
        </a>
      </span>
    );
  }
  return <span>{link}</span>;
}
