import { ReactNode, useEffect } from 'react';
import Button from '../../atoms/Button';

interface IOverlayPopupProps {
  onClose: (e: boolean) => void;
  children: ReactNode;
}

export default function OverlayPopup(props: IOverlayPopupProps) {
  const { onClose, children } = props;
  useEffect(() => {
    window.addEventListener('keydown', (e) => {
      if (e.key === 'Escape') {
        onClose(false);
      }
    });
  }, []);
  return (
    <div className="fixed top-0 left-0 z-10 min-h-screen w-full bg-black/80 backdrop-blur-sm">
      <div className="absolute right-5 top-5">
        <Button display="block" color="white" onClick={() => onClose(false)}>
          <i className="bi bi-x-lg" />
        </Button>
      </div>
      <div className="mt-20 px-14 text-white">{children}</div>
    </div>
  );
}
