import { IVacancyCard } from '../../../interfaces';
import ButtonLink from '../../atoms/ButtonLink';

interface IVacancyCardProps {
  vacancy: IVacancyCard;
}

export default function VacancyCard(props: IVacancyCardProps) {
  const { vacancy } = props;
  return (
    <li key={vacancy.link} className="mb-4">
      <div className="flex w-3/4 justify-between border-b-2 border-transparent pb-2 hover:border-b-2 hover:border-white">
        <div>
          <p className="text-2xl font-medium">{vacancy.title}</p>
          <p className="text-lg">{vacancy.location}</p>
        </div>
        <div>
          <ButtonLink isNativeAnchor href={vacancy.link} display="block">
            Lamar
          </ButtonLink>
        </div>
      </div>
    </li>
  );
}
