import LinkTo from '../../atoms/LinkTo/LinkTo';

export default function LogoGroup() {
  return (
    <div className="flex gap-3">
      <LinkTo href="/">
        <img
          src="/img/logos/logo-ipb.png"
          alt="logo ipb"
          className="h-14 w-14"
        />
      </LinkTo>
      <LinkTo href="/">
        <img
          src="/img/logos/logo-cda.png"
          alt="logo cda"
          className="h-14 w-15"
        />
      </LinkTo>
    </div>
  );
}
