import { ReactNode } from 'react';

interface Props {
  children?: ReactNode;
  className?: string;
  display: 'block' | 'inline-block' | 'flex' | 'inline-flex' | 'inline';
  color?: 'primary' | 'warning' | string;
  onClick?: () => void;
  disabled?: boolean;
}

export default function Button(props: Props) {
  const { display, children, className, color, onClick, disabled } = props;
  return (
    <button
      type="button"
      disabled={disabled}
      onClick={onClick}
      className={`${className || ''} ${display || 'block'}
     rounded-lg bg-${
       color || 'primary'
     } p-3 px-4 hover:shadow-lg disabled:cursor-not-allowed disabled:bg-gray-300 disabled:text-gray-600
    `}
    >
      {children}
    </button>
  );
}
