import LinkTo from '../../atoms/LinkTo/LinkTo';
import { headerMenuList } from './Header';

export default function MenuMobile() {
  return (
    <ul className="fixed right-0 top-20 left-0 block gap-10 bg-primary px-6 font-primary text-white shadow-lg transition-all duration-300">
      {headerMenuList.map(({ href, title }) => (
        <li key={href} className="mb-3">
          <LinkTo
            href={href}
            className=" underline-offset-2 hover:text-white/70 hover:underline"
          >
            {title.toUpperCase()}
          </LinkTo>
        </li>
      ))}
    </ul>
  );
}
