import { useParams } from 'react-router-dom';

interface IButtonPopupProps {
  onClick: (toggle: boolean) => void;
  title: 'Webinar' | 'Vacancy' | 'Profile' | 'Konsultasi';
}

export default function ButtonPopup(props: IButtonPopupProps) {
  const { onClick, title } = props;
  const params = useParams();
  const companyId = params?.companyId;
  return (
    <button
      onClick={() => {
        if (companyId === 'bri' && title === 'Vacancy') {
          window.open('https://e-recruitment.bri.co.id/');
        } else if (companyId === 'cda' && title === 'Vacancy') {
          window.open('https://cda.ipb.ac.id/vacancy/site');
        } else if (companyId === 'cda' && title === 'Webinar') {
          window.open('https://cda.ipb.ac.id/eventlist/site/index/event');
        } else {
          onClick(true);
        }
      }}
      type="button"
      className="rounded-lg border border-white/25 py-2 px-3 text-lg outline-white/80 duration-100 hover:bg-white/50 focus:outline"
    >
      {title}
    </button>
  );
}
