import { Helmet } from 'react-helmet';
import { titlePageGenerator } from '../../../utils';
import LinkTo from '../../atoms/LinkTo/LinkTo';

export default function NotFound() {
  return (
    <main className="flex h-screen w-full flex-col items-center justify-center bg-primary">
      <Helmet>
        <title>{titlePageGenerator('Halaman Tidak Ditemukan')}</title>
      </Helmet>
      <h1 className="mb-3 text-lg text-white">Halaman tidak ditemukan</h1>
      <LinkTo
        href="/"
        className="select-none rounded-sm border p-3 text-white hover:shadow-xl"
      >
        Kembali Ke Halaman Utama
      </LinkTo>
    </main>
  );
}
