import { useState } from 'react';
import { ICompanyDetail } from '../../../../interfaces';
import Navigation3D from '../../../organisms/Navigation3D/Navigation3D';
import imageBackground from './booth_1x1_.png';
import ButtonPopup from '../ButtonPopup';
import CompanyProfile from '../CompanyProfile';
import Vacancy from '../Vacancy';
import Webinar from '../Webinar';

interface IProps {
  company: ICompanyDetail;
}

export default function Booth1x11(props: IProps) {
  const { company } = props;
  const [openVacancy, setOpenVacancy] = useState(false);
  const [openCompanyProfile, setOpenCompanyProfile] = useState(false);
  const [openWebinar, setOpenWebinar] = useState(false);

  return (
    <>
      {openCompanyProfile && (
        <CompanyProfile onClose={setOpenCompanyProfile} company={company} />
      )}
      {openVacancy && <Vacancy onClose={setOpenVacancy} company={company} />}
      {openWebinar && <Webinar onClose={setOpenWebinar} company={company} />}

      <div className="relative h-screen overflow-hidden">
        <Navigation3D href="/expo-hall" />
        <img
          className="absolute top-0 left-0 -z-10 h-screen w-full object-cover shadow-lg"
          src={company.boothBackground || imageBackground}
          alt=""
        />
        {company.embedVideo && (
        <div className="fixed left-[43vw] top-[35vh] h-[20.7vh] w-[18vw]  bg-purple-700/10">
          <iframe
            className="h-full w-full rounded"
            src={company.embedVideo}
            title="YouTube video player"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          />
        </div>
        )}
        <div className="flex h-screen w-full justify-center">
          <div className="flex h-screen w-[34vw] flex-col items-end justify-end bg-purple-300/0">
            <div className="mb-[10.5vh] flex w-[55%] items-center justify-center gap-2.5 bg-green-500/0 p-10 lg:h-32 xl:h-36">
              <ButtonPopup title="Profile" onClick={setOpenCompanyProfile} />
              {company.vacancies && (
              <ButtonPopup title="Vacancy" onClick={setOpenVacancy} />)}
              {company.webinar && (
              <ButtonPopup title="Webinar" onClick={setOpenWebinar} />
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
