import companies from '../data/companies';

export const arrayPaginate = (
  array: any[],
  pageSize: number,
  pageNumber: number
) => array.slice((pageNumber - 1) * pageSize, pageNumber * pageSize);

export const getDayString = ({ day }: { day: number }) => {
  const dayStringIndonesia = [
    'Minggu',
    'Senin',
    'Selasa',
    'Rabu',
    'Kamis',
    'Jumat',
    'Sabtu',
  ];
  return dayStringIndonesia[day];
};

export const webinarFilter = () => {
  const filteredCompanies = companies.filter((company) => company.webinar);
  const webinars = filteredCompanies.map((company) => company.webinar);
  const final: any = [];
  webinars.forEach((webinar) => {
    webinar?.forEach((webinarCard) => {
      final.push(webinarCard);
    });
  });
  return final;
};

export const report = {};
