import { createContext, ReactNode, useState } from 'react';

export const AppContext = createContext({
  showMenu3D: false,
  toggleMenu: (e: boolean) => {},
});

interface Props {
  children: ReactNode;
}

export default function AppContextProver(props: Props) {
  const { children } = props;
  const [showMenu3D, setShowMenu3D] = useState(false);
  const toggleMenu = (e: boolean) => {
    setShowMenu3D(e);
  };
  return (
    <AppContext.Provider value={{ showMenu3D, toggleMenu }}>
      {children}
    </AppContext.Provider>
  );
}
