import { ICompanyDetail } from '../../../../interfaces';

const erasmus: ICompanyDetail = {
  id: 'erasmus',
  name: 'ERASMUS+ Scholarship',
  address:
    '',
  boothType: '1x1_1',
  boothBackground: '/img/booth/erasmus.png',
  contacts: [
    {
      icon: 'envelope',
      title: 'delegation-indonesia-erasmus-mundus@eeas.europa.eu',
    },
  ],
  image: '/img/logos/erasmus.png',
  profile: `<p style="text-align:center;font-size:25px;">WHAT is Erasmus+ Scholarship?</p><br>

  Erasmus+, or Erasmus Plus, is the European Union (EU) funded programme for education, training, youth and sport.<br><br>
  
  This brochure focuses only on Erasmus+ scholarship opportunities for master's degree students and scholars who are interested in studying, teaching or conducting research in Europe.<br><br>
  
  <p style="text-align:center;font-size:25px;">WHAT types of scholarships are available? </p>

  <p style="text-align:center;font-size:20px;">Types of Scholarships offered:</p>

  1. <strong>Erasmus Mundus Joint Masters (EMJM)</strong> - an integrated master's programme delivered by a consortium of higher education institutions from different countries which lead to the award of mutually recognised joint, double or multiple master's degree.<br>

  2.<strong>Scholars</strong> who want to teach or carry out research and scholarly work at an institution participating in the EMJM up to a maximum of three months.<br><br>

  <p style="text-align:center;font-size:20px;">Eligibility</p><br>

  <strong>Master's Degree Student</strong><br>
  - A third-country* national<br>
  - Not a resident of any of the EU Member States<br>
  - Has not carried out his or her main activity (studies, work, etc.) for more than a total of 12 months over the last five years in any of the EU Member States<br>
  - Has completed the relevant degree before the enrolment deadline in the EMJM programme<br><br>

  <strong>Scholar</strong><br>
  - A third-country scholar enrolled in any third-country higher education institution<br>
  - Not a resident of any of the EU Member States<br>
  - A scholar enrolled in a European higher education institution which act as a full partner institution for the purpose of contributing to the joint programme<br>
  - Has outstanding academic and/or professional experience<br><br>

  *Indonesia is in the third-country category<br><br>

  <p style="text-align:center;font-size:25px;">WHAT is a Consortium?<p><br>

  A consortium is a group of at least three higher education institutions from three different European countries which has been selected by the European Commision (EC) -  the executive body of the EU - to offer an EMJM.<br><br>

  <p style="text-align:center;font-size:25px;">ERASMUS Degree<p><br>

  <strong>Erasmus Mundus Joint Masters (EMJM): Follow an entire or multiple degree(s) in Europe</strong><br>

  - An EMJM is an integrated study programme offered by a Consortium of universities.<br>
  - An EMJM leads to the award of a recognised joint, double or multiple degree(s).<br>
  - An EMJM consists of 60, 90 or 120 European Credit Transfer System (ECTS) credits at master level, thus lasting from one to two academic years.<br>
  - The EMJM covers a wide range of academic disciplines: agricultural and forestry sciences, business studies and management, communication and information, education and teacher training, engineering and technology, humanities, law etc.<br><br>

  <p style="text-align:center;font-size:25px;"> WHAT does the scholarship cover?</p><br>

  <strong>For master's degree student, the EMJM scholarship includes:</strong><br>
  a) A contribution to your participation costs (tuition fees, full insurance coverage and any other mandatory costs related to your participation in the EMJM study programme).<br>
  b) A contribution to your travel and installation costs.<br>
  c) A monthly subsistence allowance for the entire duration of the EMJM study programme.<br><br>

  The funds are paid through the Consortium.<br><br>

  Master's Degree Student<br>
  € 24,000 per student for a one-year course<br>
  € 48,000 per student for a two-year course<br><br>
  
  <p style="text-align:center;font-size:25px;"> HOW and WHEN to apply?</p><br>

  Indonesians interested in an Erasmus+ Scholarship have to apply directly to the Consortium offering the particular EMJM programme of their choice. The application period is usually opened in September or October, for courses starting the following academic year.<br><br>

  Application procedures and deadlines are vary between courses.<br><br>

  Visit the catalogue of EMJM at https://bit.ly/ErasmusCatalogue to find over 100 Master’s programmes offering Erasmus+ scholarships.<br><br>


  <p style="text-align:center;font-size:25px;"> IS the scholarship applicable for other degree programmes?</p><br>

  No. The scholarships are exclusively reserved for undertaking Erasmus+ joint programmes (EMJM). In other words, it is not applicable for students and scholars who have been accepted by other master’s degree programmes in Europe or elsewhere in the world.<br><br>
  
  <p style="text-align:center;font-size:25px;">Application PROCESS</p><br>

  <strong>QUICK STEPS to apply for an Erasmus+ Scholarship</strong><br>

  Step #1<br>

  Review all the Master’s (EMJM) programmes offering Erasmus+ Scholarships for the next academic year: https://bit.ly/ErasmusCatalogue<br>

  <p style="text-align:center;font-size:20px;">🡻</p>

  Select the course which you are interested in and access its website to obtain specific details such as course description, prerequisites, the application process, forms and deadlines.<br>

  <p style="text-align:center;font-size:20px;">🡻</p>

  Apply directly to the Consortium via its website.<br><br>

  Step #2<br>

  Following the deadline for application, the Consortium will select applicants on the basis of the CV, study and academic merits, and motivation.<br><br>

  Step #3<br>

  The Consortium proposes a list of successful applicants to the EC Headquarters in Brussels for the final approval (by the end of February).<br>

  <p style="text-align:center;font-size:20px;">🡻</p>

  The EC confirms the selection and assigns the grants (by the end of March).<br><br>

  Step #4<br>

  The Consortium will notify applicants if they have been accepted to the course and if they have been awarded a scholarship (usually in May).<br>

  <p style="text-align:center;font-size:20px;">Pass? ✔ 🗶</p>

  Step #5: Bon voyage to Europe!


  <p style="text-align:center;font-size:25px;"> WHERE can I find more information?</p>

    Erasmus+ Scholarship<br>
    http://bit.ly/ErasmusCatalogue<br><br>

    Still have question? Send an e-mail to<br>
    delegation-indonesia-erasmus-mundus@eeas.europa.eu<br><br>

    Indonesian Erasmus Mundus Alumni<br>
    http://emundus.wordpress.com<br><br>

    Have questions for the Indonesian Erasmus Mundus alumni?<br>
    tanyatanyaem@gmail.com<br><br>

    Erasmus+<br>
    http://ec.europa.eu/erasmus-plus<br><br>

    Erasmus Mundus Students and Alumni Association<br>
    http://www.em-a.eu<br><br>

    Study in Europe<br>
    http://ec.europa.eu/education/study-in-europe<br><br>

    Study Portals<br>
    http://www.studyportals.com<br><br>

    Scholarship offers in Europe<br>
    http://scholarshipportal.com<br><br>

    DELEGATION OF THE EUROPEAN UNION TO INDONESIA<br>
    e-mail: delegation-indonesia-erasmus-mundus@eeas.europa.eu http://eeas.europa.eu/indonesia
    `,
  webinar: [
    {
      id: Math.random() * Math.random(),
      date: '2022-06-19',
      time: '11:00-12:00',
      location: 'Zoom',
      link: 'https://cda.ipb.ac.id/eventlist/site/view/82',
      speaker: {
        name: 'Tatwadhika Rangin Siddhartha',
        profession: 'Erasmus+ Indonesia Alumnus',
        photo: '/img/logos/erasmus.png',
        company: 'Erasmus+ Scholarship',
      },
      title:
        'Erasmus+ Scholarship Information',
    },
  ],
};

export default erasmus;
