import { useEffect, useRef } from 'react';
import { isMobile } from 'react-device-detect';
import { Helmet } from 'react-helmet';
import { useParams } from 'react-router-dom';
import companies from '../../../data/companies';
import useOnLoadImages from '../../../hooks/useOnLoadImages';
import { titlePageGenerator } from '../../../utils';
import TawkToWrapper from '../../atoms/TawkToWrapper/TawkToWrapper';
import TransitionLayer from '../../atoms/TransitionLayer/TransitionLayer';
import NotFound from '../NotFound/NotFound';
import OpenInComputer from '../OpenInComputer/OpenInComputer';
import Booth1x11 from './Booth1x11';
import Booth2x11 from './Booth2x11';
import Booth2x12 from './Booth2x12';

export default function Booth() {
  const params = useParams();
  const filter = companies.find((company) => company.id === params.companyId);
  const wrapperRef = useRef<HTMLDivElement>(null);
  const imagesLoaded = useOnLoadImages(wrapperRef);

  useEffect(() => {
    if (filter?.id === 'blankCompany') {
      window.location.href = '/expo-hall';
    }
  }, []);

  if (isMobile) {
    return <OpenInComputer />;
  }

  return (
    <>
      <Helmet>
        <title>
          {titlePageGenerator(filter?.name || 'Halaman Tidak Ditemukan')}
        </title>
      </Helmet>
      <div ref={wrapperRef}>
        {filter && filter.tawkTo && (
          <TawkToWrapper
            widgetId={filter.tawkTo.widgetId}
            propertyId={filter.tawkTo.propertyId}
          />
        )}
        {filter?.boothType === '2x1_1' && <Booth2x11 company={filter} />}
        {filter?.boothType === '2x1_2' && <Booth2x12 company={filter} />}
        {filter?.boothType === '1x1_1' && <Booth1x11 company={filter} />}
        {!filter && <NotFound />}
      </div>
      <TransitionLayer showTransition={imagesLoaded} />
    </>
  );
}
