/* eslint-disable react/jsx-one-expression-per-line */
import { monthNames } from '../../../data';
import { getDayString } from '../../../helpers';

export default function DateEasyRead({
  date,
  newLine,
  noNeedTime,
}: {
  date: string;
  newLine?: boolean;
  noNeedTime?: boolean;
}) {
  const dateObj = new Date(date);
  const hours = dateObj.getHours().toLocaleString('en-US', {
    unitDisplay: 'long',
  });
  const minutes = dateObj.getMinutes().toLocaleString('en-US', {
    unitDisplay: 'long',
  });
  const seconds = dateObj.getSeconds().toLocaleString('en-US', {
    unitDisplay: 'long',
  });
  return (
    <span>
      {getDayString({ day: dateObj.getDay() })},
      <span className="ml-2 inline-block">
        {dateObj.getDate()} {monthNames[dateObj.getMonth()]}{' '}
        {dateObj.getFullYear()}
      </span>
      {!noNeedTime && (
        <span>
          {newLine ? <br /> : ', '}
          {hours.length === 1 ? `0${hours}` : hours}:
          {minutes.length === 1 ? `0${minutes}` : minutes}:
          {seconds.length === 1 ? `0${seconds}` : seconds}
        </span>
      )}
    </span>
  );
}
