import LinkTo from '../../atoms/LinkTo/LinkTo';
import { headerMenuList } from './Header';

interface Props {
  active?: string;
}

export default function MenuDekstop(props: Props) {
  const { active } = props;
  return (
    <ul className="hidden flex-col justify-center gap-10 font-primary text-white lg:flex lg:flex-row">
      {headerMenuList.map(({ href, title }) => (
        <li key={href}>
          <LinkTo
            href={href}
            className={`underline-offset-2 hover:text-white/70 hover:underline ${
              active === href ? 'underline' : ''
            }`}
          >
            {title.toUpperCase()}
          </LinkTo>
        </li>
      ))}
    </ul>
  );
}
