import { ICompanyDetail } from '../../../interfaces';
import ButtonLink from '../../atoms/ButtonLink';
import DateEasyRead from '../../atoms/DateEasyRead/DateEasyRead';
import CompanyInformation from './CompanyInformation';
import OverlayPopup from './OverlayPopup';
import VacancyCard from './VacancyCard';

interface ICompanyProfileProps {
  company: ICompanyDetail;
  onClose: (e: boolean) => void;
}

export default function Webinar(props: ICompanyProfileProps) {
  const { company, onClose } = props;
  return (
    <OverlayPopup onClose={onClose}>
      <div className="flex justify-between gap-10">
        <CompanyInformation company={company} />
        <div className="flex-1 font-primary">
          <h1 className="text-3xl font-semibold">Webinar</h1>
          <ul className="mt-5 h-[75vh] overflow-auto">
            {company.webinar?.map((webinar) => (
              <li key={webinar.id} className="mb-5">
                <div className="flex w-4/5 justify-between">
                  <div>
                    <h2 className="mb-1 text-lg font-medium">
                      {webinar.title}
                    </h2>
                    <div className="flex gap-5">
                      <div>
                        <i className="bi bi-calendar3 mr-2" />
                        <DateEasyRead date={webinar.date} noNeedTime />
                      </div>
                      <div>
                        <i className="bi bi-clock mr-2" />
                        <span>{webinar.time}</span>
                      </div>
                      <div>
                        <i className="bi bi-geo mr-2" />
                        <span>{webinar.location}</span>
                      </div>
                    </div>
                    <div className="mt-1 flex items-start gap-2">
                      <i className="bi bi-person-check-fill" />
                      <div>
                        <p>{webinar.speaker.name}</p>
                        <p>{webinar.speaker.profession}</p>
                      </div>
                    </div>
                  </div>
                  <div>
                    <ButtonLink
                      isNativeAnchor
                      href={webinar.link}
                      display="block"
                    >
                      Lamar
                    </ButtonLink>
                  </div>
                </div>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </OverlayPopup>
  );
}
