/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import { useContext } from 'react';
import { AppContext } from '../../../context/AppContext';
import LinkTo from '../../atoms/LinkTo/LinkTo';
import LogoGroup from '../../molecules/Employer/EmployerHeader/LogoGroup';
import { headerMenuList } from '../../molecules/Header/Header';

function OverLay() {
  const ctx = useContext(AppContext);
  return (
    // eslint-disable-next-line jsx-a11y/control-has-associated-label
    <div
      className="fixed top-0 left-0 z-[99] h-screen w-full bg-gray-800/60 backdrop-blur-sm"
      onClick={() => {}}
      role="button"
      tabIndex={0}
      onKeyPress={() => ctx.toggleMenu(false)}
      onAuxClick={() => ctx.toggleMenu(false)}
    />
  );
}
export default function Menu3D() {
  const { toggleMenu } = useContext(AppContext);

  return (
    <>
      <OverLay />
      <div className="fixed top-0 right-0 z-[100] flex h-screen w-full justify-end">
        <div className="w-[30vw] bg-primary p-4 shadow-lg">
          <div className="flex items-center justify-between">
            <LogoGroup />
            <button
              type="button"
              className="h-10 w-10 rounded-lg bg-white text-primary shadow ring-white ring-offset-2 ring-offset-primary hover:ring-2"
              title="Close"
              onClick={() => toggleMenu(false)}
            >
              <i className="bi bi-x-lg" />
            </button>
          </div>
          <ul className="mt-4">
            {headerMenuList.map((menu) => (
              <li key={menu.href} onClick={() => toggleMenu(false)}>
                <LinkTo
                  className="mb-2 block rounded-lg p-3 text-white outline outline-1 outline-white hover:text-white/70 hover:outline-white/40 "
                  href={menu.href}
                >
                  {menu.title}
                </LinkTo>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </>
  );
}
